import { mapMutations, mapGetters,mapActions } from 'vuex'
import BaseLoding from '@/components/public/BaseLoding'

export const details = {
    methods: {
        ...mapMutations({
            setGoodDetails: 'GOODSDETAILS',
        }),
        details(val) {
            this.setGoodDetails(val)
            this.$router.push({ path: `/details`, query: { id: val.goodsId || val.id || val.cid} })
        }
    }
}

export const loading = {
    data() {
        return {
            showFlag: true,     //lodding
        }
    },

    components: {
        BaseLoding,
    },
}

// 
export const goBack = {
    methods: {
        back() {
            window.history.length > 1
                ? this.$router.go(-1)
                : this.$router.push('/home')

        }
    },
}

export const page = {
    data() {
        return {
            dataArr: [],
            total: null,
            loading: false, // 
        }
    },

    methods: {
        // newArr, 
        setNewData(newArr) {
            this.dataArr  = this.dataArr.concat(newArr)
        },

        // 
        getCurrentStart() {
            return this.dataArr.length
        },

        // 
        hasMore() {
            // 
            console.log(this.dataArr.length);
            console.log(this.total);
            
            if (this.dataArr.length >= this.total) {
                return false
            }
            return true
        },

        // 
        setTotal(total) {
            this.total = total
        },

        // 
        clearArr() {
            this.setData({
                dataArr: []
            })
            this.total = null
        },

        // 
        isLocked() {
            return this.loading ? true : false
        },

        // 
        locked() {
            this.loading = true
        },

        unLocked() {
            this.loading = false
        }
    }
}



